<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="trouble-page common-page create-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="隐患详情" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <detail v-if="detail.id" :data="detail"></detail>

    <template >
      <van-cell-group>
        <van-cell class="detail-title" title="临时解决措施："></van-cell>
        <van-field v-model="detail.tempSolution" :readonly="detail.finished || detail.status != 'pending_disposal'" placeholder="请填写" type="textarea" :rows="1" autosize />

        <van-cell class="detail-title" title="最终解决措施："></van-cell>
        <van-field v-model="detail.finalSolution" :readonly="detail.finished || detail.status != 'pending_disposal'" placeholder="请填写" type="textarea" :rows="1" autosize />

        <van-cell class="detail-title" title="备注："></van-cell>
        <van-field v-model="detail.remark" :readonly="detail.finished || detail.status != 'pending_disposal'" placeholder="请填写" type="textarea" :rows="1" autosize />
      </van-cell-group>

      <van-cell-group v-if="detail.is_duty_user_id && detail.is_duty_user_id.invisible && !detail.is_duty_user_id.readonly">
        <van-cell title="责任人：" >
          <span>{{detail.dutyUser.name}}</span>
        </van-cell>
        <van-cell title="计划完成日期：" >
          <span>{{detail.planFinishDate}}</span>
        </van-cell>
        <van-cell title="联合部门（工区）：">
          <span>{{detail.unionArea.map(item => item.name).join('、')}}</span>
        </van-cell>
      </van-cell-group>

      <van-cell-group v-if="detail.is_duty_user_id && detail.is_duty_user_id.invisible && detail.is_duty_user_id.readonly">
        <van-cell required title="责任人：" is-link clickable @click="handleSelectUser">
          <span>{{dutyUser && dutyUser.name || '请选择'}}</span>
        </van-cell>
        <van-cell required title="计划完成时间：" is-link clickable @click="handleSelectPlanDate">
          <span>{{planDate || '请选择'}}</span>
        </van-cell>
        <van-cell title="联合工区：" is-link clickable @click="handleSelectUnion">
          <!-- <span>{{ form.exameDepartment && form.exameDepartment.name }}</span> -->
          <span>{{union.map(item => item.name).join('、') || '请选择'}}</span>
        </van-cell>
      </van-cell-group>

      <van-cell-group v-if="detail.is_completion_tm && detail.is_completion_tm.invisible && detail.is_completion_tm.readonly">
        <van-cell required title="实际完成时间：" is-link clickable @click="handleSelectCompleteDate">
          <span>{{completeForm.date || '请选择'}}</span>
        </van-cell>
        <van-cell required class="detail-title" title="整改情况："></van-cell>
        <van-field v-model="completeForm.content" placeholder="请填写" type="textarea" :rows="1" autosize />

        <van-cell class="detail-title" title="整改情况："></van-cell>
        <div class="images-container">
          <div class="image-item-show" v-for="(item, index) in completeForm.imageList" :key="index" @click="handlePlayImages(index)">
            <!-- <img :src="item.url" @click="handlePlayImages(index)"> -->
            <auto-image
              :src="item.url"
              :width="108"
              :height="108"
              style="margin-bottom: 12px;margin-left: 12px;"
            ></auto-image>
            <div class="image-remove" @click.stop="handleImageRemove(index)"></div>
          </div>
          <div class="image-item" style="background: #fff;" @click.stop="handlePhoto1">
            <img src="../../assets/images/image-add.png" />
          </div>
        </div>
      </van-cell-group>
      <van-cell-group v-if="detail.is_completion_tm && detail.is_completion_tm.invisible && !detail.is_completion_tm.readonly">
        <van-cell title="实际完成时间：" >
          <span>{{detail.completeDate}}</span>
        </van-cell>

        <van-cell class="detail-title" title="整改情况："></van-cell>
        <van-field disabled :value="detail.completeContent" type="textarea" :rows="1" autosize />

        <van-cell title="整改情况：" class="detail-title" v-if="detail.completeImages.length" />
        <div class="images-container" v-if="detail.completeImages.length">
          <div class="image-item-show" v-for="(item, index) in detail.completeImages" :key="index" @click="handlePlayImages(index)">
            <!-- <img :src="item.url" @click="handlePlayImages(index)"> -->
            <auto-image :src="item.url" :width="108" :height="108" style="margin-bottom: 12px;margin-left: 12px;"></auto-image>
          </div>
        </div>
      </van-cell-group>
    </template>

    <template v-if="detail.extension_tm">
      <van-cell-group>
        <van-cell title="延期完成日期：">
          <span>{{detail.extension_tm}}</span>
        </van-cell>
        <van-cell class="detail-title" title="延期说明："></van-cell>
        <van-field disabled :value="detail.extension_explain"  type="textarea" :rows="1" autosize />

      </van-cell-group>
    </template>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action" v-if="detail.status == 'pending_disposal' && detail.is_approval">

        <template v-if="detail.dutyUser && detail.planFinishDate && !detail.finished">
          <van-button class="action-item-main" color="#FF6166" @click="handleNoPass">驳回</van-button>
          <van-button class="action-item-main" color="#108EE9" @click="handlePass">同意</van-button>
          
        </template>
        <template v-else-if="detail.finished">
          <van-button class="action-item-main" color="#FF6166" @click="handleApply">申请延期</van-button>
          <van-button class="action-item-main" color="#108EE9" @click="handleSubmit2">提交</van-button>
        </template>
        <template v-else>
          <van-button class="action-item-main" color="#108EE9" @click="handleSubmit">提交</van-button>
        </template>
        
        <!-- <van-button class="action-item-main" color="#FF6166" @click="handleReject">拒绝</van-button> -->
        <!-- <van-button class="action-item-main" color="#108EE9" @click="handleAgree">同意</van-button> -->
      </div>

      <div class="footer-action" v-if="detail.status == 'to_be_config'">
        <van-button class="action-item-main" color="#FF6166" @click="handleNoPass2">驳回</van-button>
        <van-button class="action-item-main" color="#108EE9" @click="handleClose">关闭问题</van-button>
      </div>

      <div class="footer-action" v-if="detail.status == 'postponed'">
        <van-button class="action-item-main" color="#108EE9" @click="handleApply">申请延期</van-button>
      </div>

      <div class="footer-action" v-if="detail.status == 'exceeding'">
        <van-button class="action-item-main" color="#FF6166" @click="handleSubmit">拒绝</van-button>
        <van-button class="action-item-main" color="#108EE9" @click="handleSubmit">同意</van-button>
      </div>

      <van-popup v-model="dateShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          v-model="currentTime"
          type="date"
          @confirm="handleDateConfirm"
          @cancel="dateShow = false"
        />
      </van-popup>

      <van-popup
        v-model="userShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择责任人</div>
        <a-scroll ref="filterScroller" v-if="userShow" class="filter-scroll">
          <van-radio-group v-model="userRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                clickable
                @click="userRadio = {...item}"
                v-for="(item, index) in userOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onUserSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="unionShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择联合工区</div>
        <a-scroll ref="filterScroller" v-if="unionShow" class="filter-scroll">
          <van-checkbox-group v-model="unionCheckbox">
            <van-cell-group>
              <van-cell
                :title="item.name"
                clickable
                @click="$refs.unionCheckbox[index].toggle()"
                v-for="(item, index) in areaOptions"
                :key="index"
              >
                <van-checkbox slot="right-icon" :name="item" ref="unionCheckbox" />
              </van-cell>
            </van-cell-group>
          </van-checkbox-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onUnionSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="applyShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">申请延期</div>
        <div class="detail-container">
          <van-cell required title="延期完成日期：" is-link clickable @click="handleSelectApplyDate">
            <span>{{applyForm.date || '请选择'}}</span>
          </van-cell>
          <van-cell required class="detail-title" title="延期说明："></van-cell>
          <van-field v-model="applyForm.content" placeholder="请填写延期说明" type="textarea" :rows="2" autosize />

        </div>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onApplySelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="nopassShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">驳回原因</div>
        <div class="detail-container">
          <van-field v-model="nopassReason" placeholder="请填写驳回原因" type="textarea" :rows="2" autosize />

        </div>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="handleConfirmBack">确定</van-button>
        </div>
      </van-popup>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import Detail from './Detail'
import moment from 'moment'
import AScroll from '../../components/Scroll'
import AutoImage from '../../components/AutoImage'
import { uploadImage } from '../../utils/upload.js'

export default {
  components: {
    Page,
    Detail,
    AScroll,
    AutoImage
  },
  data: () => ({
    headerHeightVW: 0,
    id: null,
    detail: {},
    userOptions: [],
    areaOptions: [],

    dutyUser: null,
    userShow: false,
    userRadio: null,

    currentTime: new Date(),
    dateShow: false,
    planDate: '',

    union: [],
    unionShow: false,
    unionCheckbox: [],
    applyForm: {
      date: '',
      content: ''
    },

    dateType: 'plan',
    applyShow: false,

    completeForm: {
      date: '',
      content: '',
      imageList: []
    },


    nopassShow: false,
    nopassReason: ''
  }),
  methods: {
    onReady (header, main) {
      this.headerHeightVW = (header.h + 1) * 100 / header.w

      this.id = navigate.getParam('id')

      this.status = navigate.getParam('status')

      this.loadDetail()
    },
    loadDetail () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      io.getTroubleDetail({id: this.id}).then(data => {
        this.detail = data
        this.$toast.clear()

        io.searchOrganization({
          department: this.detail.myarea.id
        }).then(data => {
          this.userOptions = data.users
        })

        io.getTroubleDepartment({parent: this.detail.myarea.id}).then(data => {
          this.areaOptions = data
        })
      }).catch(msg => {
        this.$toast(msg)
      })
    },
    onClickLeft () {
      navigate.back()
    },

    handleSubmit () {
      // navigate.back()
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: 'spinner',
        message: '正在提交...'
      })

      if (
        !this.dutyUser || 
        !this.planDate
      ) {
        this.$toast.fail('请填写所有必填项')
      } else {
        io.submitTrouble({
          id: this.detail.id,
          tempSolution: this.detail.tempSolution,
          finalSolution: this.detail.finalSolution,
          remark: this.detail.remark,
          finishTime: this.planDate,
          user: this.dutyUser.id,
          unionArea: this.union.map(item => item.id).join(',')
        }).then(data => {
          this.loadDetail()
        }).catch(msg => {
          this.$toast(msg)
        })
      }
    },

    handleSubmit2 () {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: 'spinner',
        message: '正在提交...'
      })

      if (
        !this.completeForm.date || 
        !this.completeForm.content
      ) {
        this.$toast.fail('请填写所有必填项')
      } else {
        uploadImage(this.completeForm.imageList).then(s => {
          this.completeForm.imageList = s

          io.submitTrouble({
            id: this.detail.id,
            tempSolution: this.detail.tempSolution,
            finalSolution: this.detail.finalSolution,
            remark: this.detail.remark,
            finishTime: this.detail.planFinishDate,
            user: this.detail.dutyUser.id,
            unionArea: this.detail.unionArea.map(item => item.id).join(','),
            completeDate: this.completeForm.date,
            completeContent: this.completeForm.content,
            completeImages: this.completeForm.imageList
          }).then(data => {
            this.loadDetail()
          }).catch(msg => {
            this.$toast(msg)
          })
        })
      }
    },

    handleNoPass () {
      this.nopassShow = true
    },

    handleNoPass2 () {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: 'spinner',
      })

      io.backTrouble({
        id: this.detail.id
      }).then(data => {
        this.$toast.clear()

        this.loadDetail()
      }).catch(msg => {
        this.$toast(msg)
      })
    },

    handleClose () {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: 'spinner',
      })

      io.closeTrouble({
        id: this.detail.id
      }).then(data => {
        this.$toast.clear()

        this.loadDetail()
      }).catch(msg => {
        this.$toast(msg)
      })
    },

    handleConfirmBack () {
      if (this.nopassReason) {
        this.$toast.loading({
          duration: 0,
          forbidClick: true,
          loadingType: 'spinner',
        })

        io.backTrouble({
          id: this.detail.id,
          reason: this.nopassReason
        }).then(data => {
          this.$toast.clear()

          this.nopassShow = false

          this.loadDetail()
        }).catch(msg => {
          this.$toast(msg)
        })
      } else {
        this.$toast.fail('请填写驳回原因')
      }
    },

    handlePass () {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: 'spinner',
      })

      io.passTrouble({
        id: this.detail.id
      }).then(data => {
        this.$toast.clear()

        this.loadDetail()
      }).catch(msg => {
        this.$toast(msg)
      })
    },

    onApplySelect () {
      // navigate.back()
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: 'spinner',
      })

      if (
        !this.applyForm.date || 
        !this.applyForm.content
      ) {
        this.$toast.fail('请填写所有必填项')
      } else {
        io.delayTrouble({
          id: this.detail.id,
          extension_tm: this.applyForm.date,
          extension_explain: this.applyForm.content
        }).then(data => {
          this.applyShow = false

          this.loadDetail()
        }).catch(msg => {
          this.$toast(msg)
        })
      }
    },

    handleSelectPlanDate () {
      this.dateShow = true

      this.dateType = 'plan'
    },

    handleSelectApplyDate () {
      this.dateShow = true

      this.dateType = 'apply'
    },

    handleSelectCompleteDate () {
      this.dateShow = true

      this.dateType = 'complete'
    },

    handleDateConfirm (time) {
      if (this.dateType == 'plan') {
        this.planDate = moment(time).format('YYYY-MM-DD')
        this.dateShow = false
      } else if (this.dateType == 'complete') {
        
        this.completeForm.date = moment(time).format('YYYY-MM-DD')
        this.dateShow = false
      } else {
        this.applyForm.date = moment(time).format('YYYY-MM-DD')
        this.dateShow = false
      }
    },

    handleSelectUser () {
      this.userShow = true
      this.userRadio = {...this.dutyUser}
    },

    onUserSelect () {
      this.dutyUser = {...this.userRadio}
      this.userShow = false
    },

    
    handleSelectUnion () {
      this.unionShow = true
      this.unionCheckbox = [...this.union]
    },

    onUnionSelect () {
      this.union = [...this.unionCheckbox]
      this.unionShow = false
    },

    handleApply () {
      this.applyShow = true

      if (this.detail.extension_tm) {
        this.applyForm.date = this.detail.extension_tm
      }

      this.detail.extension_explain && (this.applyForm.content = this.detail.extension_explain)
    },

    handlePhoto1() {
      api.getPicture(
        {
          sourceType: 'camera',
          destinationType: 'url',
          preview: true,
          quality: 50,
          mediaValue: 'pic'
        },
        (ret, err) => {
          // alert(JSON.stringify(ret))

          this.completeForm.imageList.push({
            url: ret.data
          })
        }
      )
    },

    handleImageRemove(index) {
      this.completeForm.imageList.splice(index, 1)
    },

    handlePlayImages(index) {
      localStorage.setItem('photos', JSON.stringify(this.completeForm.imageList))
      navigate.open('photoViewer', { index: index })
    },
  }
}
</script>

<style lang="scss">

.problem-page{
  .btn-save{
    .van-button__text{
      color: #108EE9;
    }
  }
}

</style>
